.matchResult .info {
  display: none;
}
.matchResult:hover .info {
  display: block;
}

.stepWrapper .similarityBatch {
  position: absolute;
  bottom: -75px;
  left: 50%;
  padding: 25px 11px 0px 11px;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  line-height: 76px;
  text-align: center;
  font-size: 23px;
  margin-left: -60px;
  margin-top: -60px;
  background-color: #aed581;
  overflow: hidden;
  box-shadow: 0px 2px 28px #00000057;
  outline: 7px solid #fff;
  z-index: 400;
  cursor: pointer;
  transition: 0.5s ease-out;
}

.stepWrapper .similarityBatch:hover {
  bottom: -50px;
  box-shadow: 0px 2px 28px #00000057;
}

.stepWrapper .below {
  background-color: #ff7a59;
}
